.sofax-footer-top {
    padding: 130px 0 100px;
    @include max (991px){
        padding: 100px 0 80px;
    }
    @include max (767px){
        padding: 80px 0 60px;
    }
  }
  .sofax-footer-wrap{
    img {
        margin-bottom: 20px;
    }
    p {
        margin-bottom: 45px;
    }
  }
 
  .sofax-footer-menu {
    h5 {
        margin-bottom: 24px;
    }
    ul {
        li{
            margin-bottom: 15px;
            a{
                color: var(--body-color);
            }
        }
    }
    @include max (1199px){
        margin-top: 40px;
    }
  }
 
  .sofax-footer-active {
    margin-left: 60px;
    
    @include max (1199px){
        margin-left: 0px;
    }
  }
  
  a.social-icon img {
    margin-bottom: 15px;
  }
  
  .sofax-social-icon {
    ul {
        li {
            display: inline-block;
            a {
                padding: 15px;
                border-radius: 50px;
                border: 1px solid var(--light-bg);
                background-color: var(--light-bg);
                width: 45px;
                height: 45px;
                display: flex;
                margin-right: 24px;
                align-items: center;
                transition: .3s;
                &:hover {
                    background-color: var(--accent-color);
                }
            }
        }
    }
   
  }

  .sofax-footer-bottom-text {
    display: block;
    text-align: center;
    padding: 30px 0;
    border-top: 1px solid var(--light-color);
    p {
        margin: 0;
    }
  }

  // end sofax footer v1
