.sofax-hero-thumb {
    position: relative;
    padding-top: 80px;
    @include max(991px) {
        padding-top: 60px;
        text-align: center;
    }
    @include max(767px) {
        padding-top: 40px;
    }
}
//  end sofax hero thumb v1

.sofax-content-img {
    position: relative;
    @include max(991px) {

        text-align: center;
        margin-bottom: 30px;
        
    }
}
// end sofax content thumb v1

.sofax-features-thumb {
    text-align: center;
    margin-top: 30px;
}
.features-v2 {
    background-color: var(--accent-color);
    padding: 30px 30px 0 30px;
}
// end sofax features thumb v2

.about-thumb {
    @include max (991px) {
        text-align: center;
        margin-bottom: 30px;
    }
}