.sofax-testimonial-rating {
    ul {
        li {
            display: inline-block;
        }
    }
}
.sofax-testimonial-content {
    border: 1px solid var(--border2-color);
    padding: 35px;
    border-radius: 10px;
}
.sofax-testimonial-data p {
    margin-bottom: 20px;
    font-size: 20px;
}
.sofax-testimonial-author {
    display: flex;
    gap: 10px;
}
.member-text {
    h5 {
        margin: 0;
    }
    p {
        margin: 0;
    }
}
.sofax-testimonial-slider .slick-slide{
    margin: 0 12px;
}
.sofax-testimonial-slider-2 {
    margin-top: 40px;
}
.sofax-testimonial-slider-2 .slick-slide{
    margin: 0 12px;
}

// testimonial slider v2

.sofax-testimonial {
  &-rating {
    margin-bottom: 30px;
    ul {
      li{
        display: inline-block;
      }
    }
  }
}

.testimonial {
  &-content {
    margin-bottom: 40px;
  }
  p {
    font-size: 30px;
    line-height: 40px;
  }
}

.testimonial-author {
  margin-right: 30%;
  &-icon {
    ul {
      li {
        display: inline-block;
        margin-right: 24px;
        a{
          svg {
            width: 50px;
            height: 50px;
            padding: 5px;
          }
        }
      }
    }
  }
}

.sofax-testimonial-2column-slider .sofax-t-next:hover,
.sofax-testimonial-2column-slider .sofax-t-prev svg {
  background-color: var(--green-bg);
}
.sofax-testimonial-2column-slider .sofax-t-prev:hover {
  background-color: var(--green-bg);
}
.sofax-testimonial-2column-slider .sofax-t-next, 
.sofax-testimonial-2column-slider .sofax-t-prev {
  left: 70%;
  width: 50px;
  height: 50px;
  bottom: 25px;
  z-index: 99;
  cursor: pointer;
  position: absolute;
  background-size: 30px;
  background-position: center;
  background-repeat: no-repeat;
  transition: all .4s;
  background-image: url(../images/v2/leftarrow.svg);
  border: none;
  background-color: var(--light-color);

}
.sofax-testimonial-2column-slider .sofax-t-prev {
  left: auto;
  right: 21%;
  background-image: url(../images/v2/rightarrow.svg);
  background-color: var(--light-color);
}
.sofax-testimonial-slider-content p{
  font-size: 35px;
  line-height: 45px;
}
.testimonial-author p {
  font-size: 18px;
}