
.sofax-section-padding {
    padding: 130px 0 106px;
    @include max(991px) {
        padding: 100px 0 76px;
    }
    @include max(767px) {
        padding: 80px 0 56px;
    }
}

.sofax-section-padding2 {
    padding: 130px 0;
    @include max(991px) {
        padding: 100px 0;
    }
    @include max(767px) {
        padding: 80px 0;
    }
}
.sofax-section-padding3 {
    padding: 0 0 130px;
    @include max(991px) {
        padding: 0 0 100px;
    }
    @include max(767px) {
        padding: 0 0 80px;
    }
}
.sofax-section-padding4 {
    padding: 220px 0 120px;
    @include max (991px){
        padding: 190px 0 90px;
    }
    @include max (767px){
        padding: 170px 0 70px;
    }
}
.sofax-section-title {
    padding-bottom: 80px;
    @include max(991px) {
        padding-bottom: 60px;
        text-align: center;
    }
    @include max(767px) {
        padding-bottom: 40px;
    }
    &.center{
        max-width: 650px;
        margin: 0 auto;
        text-align: center;
    }
    
}
.sofax-title-btn {
    @include max(991px) {
        width: 100%;
        text-align: center;
        margin-top: 30px;
    }
    
}

.extra-mt {
    margin-top: 50px;
}
.sofax-default-content {
    h2 {
        margin-bottom: 20px;
    }
    p {
        font-size: 20px;
    }
    @include max(991px) {
        text-align: center;
    }
}

.light-color {
    h2 {
        color: var(--white-bg);
    }
    p {
        color: var(--white-bg);
    }
}


