input[type="text"],
input[type="email"],
input[type="password"],
input[type="url"],
input[type="tel"],
input[type="time"],
input[type="date"],
input[type="search"],
input, textarea {
  margin: 0;
  width: 100%;
  height: 60px;
  font-weight: 700;
  color: #3C3C3C;
  font-size: 16px;
  border-radius: 50px;
  background-color: transparent;
  padding: 15px 24px !important;
  border: none!important;
  transition: all 0.4s;
  border: 1px solid #747474!important;
}
// input:focus, 
// textarea:focus{
//   border: 1px solid var(--accent-color)!important;
// }
textarea {
  height: 200px;
  resize: none;
  border-radius: 20px;
  @include max(767px) {
    height: 100px;
  }
}

input::-webkit-input-placeholder, 
textarea::-webkit-input-placeholder{ /* WebKit, Blink, Edge */
    color:    #3C3C3C;
    opacity: 1;
}
input:-moz-placeholder, 
textarea:-moz-placeholder{ /* Mozilla Firefox 4 to 18 */
   color:    #3C3C3C;
   opacity:  1;
}
input::-moz-placeholder, 
textarea::-moz-placeholder{ /* Mozilla Firefox 19+ */
   color:    #3C3C3C;
   opacity:  1;
}
input:-ms-input-placeholder, 
textarea:-ms-input-placeholder{ /* Internet Explorer 10-11 */
   color:    #3C3C3C;
   opacity: 1;
}
input::-ms-input-placeholder, 
textarea::-ms-input-placeholder{ /* Microsoft Edge */
   color:    #3C3C3C;
   opacity: 1;
}

input::placeholder, 
textarea::placeholder{ /* Most modern browsers support this now. */
   color:    #3C3C3C;
   opacity: 1;
}
// end default form

.sofax-subscription-field{
   max-width: 600px;
   margin: 0 auto;
   position: relative;
   
}
button#sofax-subscription-btn{
       position: absolute;
       top: 5px;
       right: 5px;
       padding: 13px 22px;
       border-radius: 50px;
       border: none;
       font-weight: 700;
       font-size: 16px;
       line-height: 26px;
       width: 230px;
       height: 50px;
      background-color: var(--accent-color);
      
      @include max(767px) {
         position: static;
         width: 100%;
         margin-top: 24px;
      }
   }